import { Button } from "@chakra-ui/react";
import { Formik, Field, Form } from "formik";
import { useState } from "react";
import { useNavigate } from "react-router";

function Tracking() {
  const [tracking, setTracking] = useState("");
  const tg = window.Telegram.WebApp;
  const backButton = tg.BackButton;
  const navigate = useNavigate();
  backButton.show();
  backButton.onClick(() => {
    backButton.hide();
    navigate("/");
  });

  async function getTrack(track_num) {
    console.log(track_num);
    console.log(
      `https://api.track24.ru/tracking.json.php?apiKey=d7bd08383f431cf8913f1eb29585c01a&domain=localhost&pretty=true&code=${track_num}`
    );
    const response = await fetch(
      `https://api.track24.ru/tracking.json.php?apiKey=d7bd08383f431cf8913f1eb29585c01a&domain=localhost&pretty=true&code=${track_num}`
    );
    const track = await response.json();
    console.log(track);

    if (track.status == "ok") {
      setTracking(
        <div className="track_info">
          <p className="track_info_header">  {track_num}</p>
          <p className="track_info_addrstate"> :</p>
          <p className="track_info_state">
            {track.data.destinationPostalAddress}
          </p>
          <p className="track_info_addrstate"> :</p>
          <p className="track_info_state">
            $
            {
              track.data.events[track.data.events.length - 1]
                .operationAttributeTranslated
            }
          </p>
        </div>
      );
    } else {
      setTracking("");
    }
  }
  return (
    <div id="main_tracking">
      <div class="dostavka_content">
        <div className="main_tracking">
          <h1
            id="dostavka_header"
            style={{ fontSize: "24px", textAlign: "center" }}
          >
            
          </h1>
          <p id="advance_help">
             - ,    .
          </p>
          <Formik
            initialValues={{ track: "   " }}
            onSubmit={(values) => getTrack(values.track)}
          >
            <Form>
              <Field
                className="gray_input"
                id="track_input"
                type="text"
                name="track"
              />
              <button type="submit"></button>
            </Form>
          </Formik>
          {tracking}
        </div>
      </div>
    </div>
  );
}
export default Tracking;
